import React from 'react';
import ReactDOM from 'react-dom';
import problemGenerator from './problemGenerator';
import rectangle1 from './images/Rectangles/reactangle1.png';
import rectangle2 from './images/Rectangles/reactangle2.png';
import rectangle3 from './images/Rectangles/reactangle3.png';
import rectangle4 from './images/Rectangles/reactangle4.png';
import rectangle5 from './images/Rectangles/reactangle5.png';
import up from "./images/62.gif";
import down from "./images/63.gif";
import './PARRECTANGLE.css';

function PARRECTANGLE(props){
    const [problem,setProblem] = React.useState(problemGenerator.generate());
    const [show,setShow] = React.useState([]);
    const [userInput1, setUserInput1] = React.useState(0);
    const [userInput2, setUserInput2] = React.useState(0);
    const [userInput3, setUserInput3] = React.useState(0);
    const [userInput4, setUserInput4] = React.useState(0);
    const [userresult,setUserResult] = React.useState({name:'result',result:''});
    const userEvaluate = userInput1+userInput2+userInput3+userInput4;
    const random = Math.floor(Math.random()*problem.lineAB.length);
    const [positions,setPositions] = React.useState([
        {
            line : 'A to B',
            number : problem.lineAB[random],
            style : {
                bottom : '30%',
                left: '50%',
                transform: 'rotate(0deg)',
                color: 'white'
            }
        },
        {
            line : 'B to C',
            number : problem.lineBC[random],
            style : {
                top : '30%',
                right: '0%',
                transform: 'rotate(-90deg)',
                color: 'white'
            }
        },
        {  
            line : 'C to D', 
            number : problem.lineAB[random],
            style : {
                top:'1%',
                left : '50%',
                transform: 'rotate(0deg)',
                color : 'white'
            }
        },
        {   
            line : 'D to A',
            number : problem.lineBC[random],
            style : {
                top : '30%',
                left : '-1%',
                transform: 'rotate(-90deg)',
                color : 'white'
            }
        }
    ]);
    const Add = (obj,idx) => {
        let newShow = [...show];
        // newShow[idx] = obj;
        newShow.splice(idx,0,obj)
        setShow(newShow);
        setPositions(positions.slice(0, idx).concat(positions.slice(idx + 1)));
    }
    const Delete = (obj,idx) => {
        let newPosition = [...positions];
        newPosition.splice(idx,0,obj)
        setShow(show.slice(0, idx).concat(show.slice(idx + 1)));
        setPositions(newPosition);
    }
    const changeImage = () => {
        if(show.length==0){
            return `url(${rectangle1})`
        }
        else if(show.length==1){
            return `url(${rectangle2})`
        }
        else if(show.length==2){
            return `url(${rectangle3})`
        }
        else if(show.length==3){
            return `url(${rectangle4})`
        }
        else if(show.length==4){
            return `url(${rectangle5})`
        }
    }
    const handleChange = (e) =>{
        // console.log(e.target.value);
        setUserResult({number:e.target.value})
    }
    const Evaluate = () =>{
        if(userEvaluate == ((problem.lineAB[random]*2)+(problem.lineBC[random]*2))){
            props.onCorrectAnswer();
        }else{
            props.onWrongAnswer();
        }
    }
    return(
        <React.Fragment>
        <div className='main1'>
            <div className='question'>
                <h6>{problem.question}</h6>
            </div>
            <div className='row1'>
                    <div className='showfigure' style={{ backgroundImage: changeImage() }}>
                        {
                            positions.map((obj, idx) => {
                                return <div className='two' style={{ ...obj.style }} onClick={() => Add(obj, idx)}>{obj.number}cm</div>
                            })
                        }
                    </div>
                    <div className='showAnswer'>
                        <ul className='list'>
                            {
                                show.map((obj, idx) => {
                                    return (
                                        <li style={{ color: obj.style.color }} onClick={() => Delete(obj, idx)}>
                                            Line segment {obj.line} = <b>{obj.number}</b>
                                        </li>
                                    )
                                })
                            }
                            {
                                show.length >= 4 ?
                                    <div className='userInput'>
                                        <div>
                                            <img src={up} width='20%' onClick={() => setUserInput1(userInput1 - 1)} />
                                            <button className='btn_result'>{userInput1}</button>
                                            <img src={down} width='20%' onClick={() => setUserInput1(userInput1 + 1)} />
                                        </div>
                                        <div className='plusDiv'>
                                            <i className='fa fa-plus plusIcon'></i>
                                        </div>
                                        <div>
                                            <img src={up} width='20%' onClick={() => setUserInput2(userInput2 - 1)} />
                                            <button className='btn_result'>{userInput2}</button>
                                            <img src={down} width='20%' onClick={() => setUserInput2(userInput2 + 1)} />
                                        </div>
                                        <div className='plusDiv'>
                                            <i className='fa fa-plus plusIcon'></i>
                                        </div>
                                        <div>
                                            <img src={up} width='20%' onClick={() => setUserInput3(userInput3 - 1)} />
                                            <button className='btn_result'>{userInput3}</button>
                                            <img src={down} width='20%' onClick={() => setUserInput3(userInput3 + 1)} />
                                        </div>
                                        <div className='plusDiv'>
                                            <i className='fa fa-plus plusIcon'></i>
                                        </div>
                                        <div>
                                            <img src={up} width='20%' onClick={() => setUserInput4(userInput4 - 1)} />
                                            <button className='btn_result'>{userInput4}</button>
                                            <img src={down} width='20%' onClick={() => setUserInput4(userInput4 + 1)} />
                                        </div>
                                        <div className='plusDiv'>
                                            <i className='fas fa-equals plusIcon'></i>
                                        </div>
                                        <div className='resultDiv'>
                                            <input className='input_result' size='3' onChange={(e) => handleChange(e)} />
                                            {/* <button className='btn_result'>{userResult}</button> */}
                                        </div>
                                    </div>

                                    :
                                    ''
                            }
                            {
                                userresult.number > 0 ?
                                    <button className="resButton" onClick={() => Evaluate()}><i className="fa fa-paper-plane"></i></button>
                                    :
                                    ''
                            }
                        </ul>
                    </div>
            </div>
            
        </div>
        </React.Fragment>
    )
}

export default PARRECTANGLE;