import getURLParams from "../../../../utils/getURLParams";

const dif = getURLParams.dif;
const range = (start,end) => {
    return Array(end - start + 1).fill().map((_, idx) => start + idx)
}

const generate = () => {
    let problem = {
        lineAB : [],
        question : ''
    }

    problem = {...problem, question:'Find the parameter of tha following figure'}

    if(dif == 'b'){
        problem = {...problem, lineAB:range(1,9)}
    }
    else if(dif == 'i'){
        problem = {...problem,lineAB:range(10,15)}
    }
    else{
        problem = {...problem,lineAB:range(15,20)}
    }

    return problem
}

export default {generate};