import React from 'react'


import catRun from './sprites/run/cat.png'
import guyRun from './sprites/run/guy.png'
// import Spritesheet from 'react-responsive-spritesheet';
import { useEffect } from 'react';

export default function Balance() {
    const [left, setLeft] = React.useState(40)
    const [guyLeft, setGuyLeft] = React.useState(20)
    const [intervalId, setIntervalId] = React.useState(0);

    const handleClick = () => {
        if (intervalId) {
            clearInterval(intervalId);
            setIntervalId(0);
            return;
        }
        const newIntervalId = setInterval(() => {
            setLeft(left + 10);
        }, 1000);
        setIntervalId(newIntervalId);
    };

    return (
        <div style={{ display: "flex" }}>

            {/* <div style={{ position: "absolute", width: "100vw", top: "50vh", left: left + "vh", transitionDuration: "0.5s" }}>
                <Spritesheet
                    style={{ width: "40%" }}
                    image={guyRun}
                    widthFrame={415}
                    heightFrame={507}
                    steps={10}
                    fps={10}
                    autoplay={true}
                    loop={true}
                />
            </div> */}
            <div style={{ position: "absolute", width: "100vw", top: "50vh", left: left + "vh", transitionDuration: "0.5s" }}>
                {/* <Spritesheet
                    style={{ width: "200px" }}
                    image={catRun}
                    widthFrame={542}
                    heightFrame={474}
                    steps={7}
                    fps={10}
                    autoplay={true}
                    loop={true}
                    onClick={handleClick}
                /> */}
            </div>
            {/* <img src={runAnimation[index]} /> */}
        </div>
    )
}
