import getURLParams from "../../../utils/getURLParams";

const dif = getURLParams.dif;
const range = (start,end) => {
    return Array(end - start + 1).fill().map((_, idx) => start + idx)
}

const numerator = [];
for(var j=0;j<=10;j++){
  numerator.push(2*j+1);
}
const denominator=[];
for(var i=11;i<=20;i++){
  denominator.push(i*2);
}
const fraction = numerator.map((obj,idx)=>{
  return { n: obj, d: denominator[idx]}
})

const Rindex = Math.floor(Math.random()*fraction.length);


const generate = () => {
    let problem = {
        numerator : [],
        denominator : [],
        question : ''
    }

    problem = {...problem, question:'Add the fractions'}

    if(dif == 'b'){
        problem = {...problem, numerator:range(1,5)}
        problem = {...problem, denominator:range(5,10)}
    }
    else if(dif == 'i'){
        problem = {...problem,numerator:range(11,15)}
        problem = {...problem,denominator:range(16,20)}
    }
    else{
        problem = {...problem,numerator:range(21,25)}
        problem = {...problem,denominator:range(16,20)}
    }

    return problem
}

export default {generate};